@import url(https://fonts.googleapis.com/css?family=Lobster);
*,*::after,*::before {
  box-sizing: border-box;
}

:root {
  --color-blue: rgb(6, 166, 240);
}

main {
  width: 100%;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(6, 166, 240);
  background-color: var(--color-blue);
  font-size: 2vw;
}

#new-quote-button {
  color: #342224;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


#quote-section{height:100vh;background-color:#e74c3c;-webkit-animation:fade-in 1s linear;animation:fade-in 1s linear;font-size:2.5vw}#quote-section #quote-block{max-width:60%;background-color:white;-webkit-animation:fade-in 1s linear;animation:fade-in 1s linear;padding:40px 60px;border-radius:3px}#quote-section #quote-block #new-quote-button{background-color:#e74c3c;color:white;-webkit-animation:fade-in 1s linear;animation:fade-in 1s linear}#quote-section #quote-block #quote-master{font-size:1.75vw}#quote-section .icon{color:#e74c3c;font-size:1.5em}@-webkit-keyframes fade-in{from{opacity:0}to{opacity:1}}@keyframes fade-in{from{opacity:0}to{opacity:1}}

